<template>
    <div class="activity-deatil-page">
        <navigation-bar
            class="detail-nav-bar"
            :pageName="title"
            :border="false"
            @onLeftClick="onBack"
        />
        <div class="detail-content" v-show="!pageNoData">
            <div class="content_title">{{currentDetailData.ACTI_NAME}}</div>
            <div class="content-image">
                <img :src="currentDetailData.PIC_URL" v-show="currentDetailData.PIC_URL" alt="">
            </div>
            <div class="content-des" v-html="currentDetailData.CONTENT"></div>
            <div class="grayBar"></div>
            <div class="detail-cell section-select" @click="sectionSelectClicked">
                <div class="left">场次</div>
                <div class="right">
                    <span>{{currentDetailData.DETAIL_NAME}}</span>
                    <van-icon name="arrow" size="15px"/>
                </div>

            </div>
            <div class="grayBar"></div>
            <div class="detail-cell">
                <div class="left">已报名人数</div>
                <div class="right">{{sectionData.ENROL_CNTS}}/{{sectionData.LIMIT_MAX}}</div>
            </div>
            <div class="grayBar"></div>
            <div class="detail-cell">
                <div class="left">报名时间</div>
                <div class="right">{{currentDetailData.START_TIME}} 至 {{currentDetailData.END_TIME}}</div>
            </div>
            <div class="grayBar"></div>
            <div class="detail-cell">
                <div class="left">活动时间</div>
                <div class="right">{{sectionData.START_TIME}} 至 {{sectionData.END_TIME}}</div>
            </div>
            <div class="activity-action">
                <div class="sign-in action" @click="signinBtnClicked">{{sectionData.CLOSE_SIGN_FLAG ? "开启签到" : "关闭签到"}}</div>
                <div class="enterfor-list action" @click="entryforListClicked">报名列表</div>
                <!-- <div class="qrcode" @click="qrcodeShowClicked" v-show="sectionData.CLOSE_SIGN_FLAG == 0">获取二维码</div> -->
                <div class="qrcode action" @click="qrcodeShowClicked" :style="{visibility: sectionData.CLOSE_SIGN_FLAG == 0 ? 'visible' : 'hidden'}">
                    获取二维码
                </div>
            </div>
        </div>

        <div class="no-list" v-show="pageNoData">
            <img :src="noSourceImg" alt="">
            <div>暂无详情</div>
        </div>

        <van-action-sheet
            v-model="isShowSelect"
            :round="true"
            :safe-area-inset-bottom="true"
            :actions="selectActions"
            cancel-text="取消"
            @select="onSelect"
            @cancel="onCancel"
        />

  </div>
</template>

<script>
  import {
    getActivityDetailService,
    getActivitySectionService,
    updateActivitysignInService
  } from "../../services/activityService";
  import {store, storage} from "../../utils/store";

  import Vue from 'vue';
  import {Toast, Icon, ActionSheet, ImagePreview } from 'vant';
  Vue.use(Toast).use(Icon).use(ActionSheet).use(ImagePreview);

  // 暂无数据图片
  import noSourceImg from '@/assets/imgs/workPlan/noSource.png'
  import NavigationBar from "../title/NavigationBar";

  export default {
    name: "activityDetail",
    components: { NavigationBar },
    created() {
        // 加载页面数据
        this.loadPageData();
    },
    beforeDestroy () {
        if(this.previewId){
            this.previewId.close();
        }
    },
    data() {
      return {
        active: 0,
        title: '活动详情',
        pageNoData: false,
        // 是否展示
        isShowSelect: false,
        // 场次选择数据源
        selectActions:[],

        // 活动详情数据
        detailData: [],
        // 当前活动数据
        currentDetailData: {},
        // 场次选择数据
        sectionData: {},

        //图片
        noSourceImg: noSourceImg,

        previewId: ""
      };
    },
    methods: {
        // 场次切换选择
        onSelect(item) {
            this.isShowSelect = false;
            if (this.currentDetailData.DETAIL_CODE != item.ele.DETAIL_CODE) {
                this.currentDetailData = item.ele;
                this.getSectionInfo(item.ele.DETAIL_CODE);
            }
        },
        // 场次切换取消
        onCancel() {
        //  点击取消
        },
        // 获取页面数据
        loadPageData: function() {
            let requestData = {
                ACTI_CODE: store.get("activityDetailCode")
            };
            getActivityDetailService(requestData)
            .then(noteData => {
                if (noteData && noteData.length > 0) {
                    this.detailData = noteData;
                    this.currentDetailData = noteData[0];
                    this.pageNoData = false;

                    // 获取场次信息
                    this.getSectionInfo(this.currentDetailData.DETAIL_CODE);

                    // 填充场次选择数据源
                    this.sectionData = [];
                    for (let index = 0; index < noteData.length; index++) {
                        const element = noteData[index];
                        this.selectActions.push({
                            name: element.DETAIL_NAME,
                            color: '#1f1f1f',
                            ele: element
                        });
                    }
                }
                else {
                    this.detailData = [];
                    this.pageNoData = true;
                }
            })
            .catch(error => {
                Toast.fail(error);
            });
        },
        // 获取场次信息
        getSectionInfo(detailCode) {
            // 获取场次信息
            let requestData = {
                DETAIL_CODE: detailCode
            };
            getActivitySectionService(requestData)
            .then(noteData => {
                this.sectionData = noteData;
            })
            .catch(error => {
                Toast.fail(error);
            });
        },
        // 点击场次选择
        sectionSelectClicked() {
            // Toast('场次选择点击');
            this.isShowSelect = true;
        },
        // 点击二维码展示
        qrcodeShowClicked(){
            // Toast('二维码展示');
            this.previewId = ImagePreview({
                images:[this.sectionData.QR_URL],
                showIndex: false
            });
        },
        // 点击报名列表查看
        entryforListClicked() {
            // Toast('报名列表查看');
            this.$router.push({
            name: "activityRegisterList",
            params: this.sectionData
          });
        },
        // 点击签到按钮
        signinBtnClicked() {
            if (this.sectionData.CLOSE_SIGN_FLAG) {
                /// 开启签到需要判断时间

                let nowDate = new Date(this.getNowDate().replace(/\-/g, "\/")).getTime();
                // 报名开始时间
                let startDate = new Date (this.currentDetailData.START_TIME.replace(/\-/g, "\/")).getTime();
                //报名结束时间
                let endDate = new Date (this.sectionData.END_TIME.replace(/\-/g, "\/")).getTime();
                if(nowDate < startDate){
                    Toast('报名未开始，无法开启签到 ！')
                    return;
                }
                else if(nowDate > endDate){
                    Toast('活动已结束，无法开启签到 ！')
                    return;
                }
            }
            // 请求接口
            let custNews = store.get("custNews");
            custNews = custNews ? custNews : storage.getStorage("custNews");
            var requestData = {
                CLOSE_SIGN_FLAG: this.sectionData.CLOSE_SIGN_FLAG ? 0 : 1,
                DETAIL_CODE:this.sectionData.DETAIL_CODE,
                USER_CODE:custNews.USER_CODE
            }
            // 更新签到标识
            updateActivitysignInService(requestData)
            .then(noteData => {
                this.sectionData.CLOSE_SIGN_FLAG = this.sectionData.CLOSE_SIGN_FLAG ? 0 : 1;
            })
            .catch(error => {
                Toast.fail(error);
            });
        },
        onBack() {
            history.back();
        },
        // 获取当前时间
        getNowDate() {
            var date = new Date();
            var seperator1 = "-";
            var seperator2 = ":";
            var month = date.getMonth() + 1;
            var strDate = date.getDate();
            if (month >= 1 && month <= 9) {
            month = "0" + month;
            }
            if (strDate >= 0 && strDate <= 9) {
            strDate = "0" + strDate;
            }
            var currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate
            + " " + date.getHours() + seperator2 + date.getMinutes()
            + seperator2 + date.getSeconds();
            return currentdate;
      },
    }
  };
</script>

