import http from "../http";

// 获取活动列表
const getActivityListService = (reqData) => {
    return new Promise((resolve, reject) => {
        http.post('/hisense-systemServer/app/property/propertyActivitiesController/getActiMsg.do',
        { data: JSON.stringify(reqData) }, true, 'homeUrl').then(noteData => {
            resolve(noteData);
        }).catch(error => {
            reject(error);
        })
    });
}

